<template>
  <div id="p_sign_request">
    <commonnav :msg="dad_text"></commonnav>
    <main>
      <div class="investors_infor">
      <h2>{{title}}</h2>
      <commoninvestors v-if="$route.query.signStatus==6" :investor_infor="investor_infor"></commoninvestors>
      </div>
      <article>
        <boxx :nav_lists="nav_lists"></boxx>
        <commondetails :toson="details_lists"></commondetails>
        <footer>
           <aside v-if="$route.query.signStatus==6">
            <button @click="review(8)"></button>
            <button @click="review(7)"></button>
          </aside>
          <aside v-else-if="$route.query.signStatus==1">
            <button @click="routerto"></button>
            <button @click="agreement(3)"></button>
          </aside>
        </footer>
      </article>
    </main>
    <mbottom></mbottom>
  </div>
</template>
<script>
export default {
  name: "goods_details",
  data() {
    return {
      show: false,
      projectId:'',
      signId:'',
      signStatus:"",
      title: "",
      dad_text: "",
     investor_infor: {
        investorsType: {
          name: ":",
          response: ""
        },
        investorsCompany: {
          name: ":",
          response: ""
        },
        investorsName: {
          name: ":",
          response: ""
        },
        investorsArea: {
          name: ":",
          response: ""
        }
      },
      nav_lists: {
        financingStage: {
          name: "",
          response: ""
        },
        committedCount: {
          name: "</br>",
          response: ""
        },
        interestProjectCount: {
          name: "<br>",
          response: ""
        },

      },
      details_lists: {
        projectIndustry: {
          name: ":",
          response: ""
        },
        projectArea: {
          name: ":",
          response: ""
        },
        // signStatu: {
        //   name: ":",
        //   response: ""
        // },
        projectCompany: {
          name: ":",
          response: ""
        },
        currencyType: {
          name: ":",
          response: ""
        },
        collectMoney: {
          name: ":",
          collectMoneyMin:'',
          collectMoneyMax: "",
        },
        projectMobile: {
          name: ":",
          response: ""
        },
        projectEmail: {
          name: ":",
          response: ""
        },
        projectDescribe: {
          name: ":",
          response: ""
        },
        projectDetail:{
          name: ":",
          response: ""
        },
      }
    };
  },
  beforeRouteLeave(to,from,next){
      console.log(to,from)
        if(to.name=='a_submit_contract'){
          // console.log(123)
           next({path: '/mysign'});
        }else{
          next()
        }
  },
  created() {
    this.projectId=this.$route.query.projectId?
    this.$route.query.projectId:'';
   
    this.signId=this.$route.query.signId?this.$route.query.signId:-1;
    this.signStatus=this.$route.query.signStatus?this.$route.query.signStatus:'';
     console.log (this.signId);
    if(this.signStatus==6){
      this.dad_text=""
    }else if(this.signStatus==1){
      this.dad_text=""
    }
    this.$loading();
    this.$global.goods_deatails(`${this.$baseurl}/bsl_web/project/getProjectDetails?projectLan=zh_CN&projectId=${this.projectId}&signStatus=${this.signStatus}&signId=${this.signId}`,'get',{},this.details_lists,this.nav_lists,this.investor_infor).then(res=>{
    this.title=res.title;
    this.$toast.clear();
    if(res.projectLifeCycle==-1){
      this.$dialog
        .alert({
          title: "",
        })
        .then(() => {
          this.$routerto("mysign");
        });
    }
  })
  },
  methods: {
    review(num){
      this.$loading();
       this.$global.post_encapsulation(`${this.$baseurl}/bsl_web/projectSign/reviewInvestorsData`,{signId:this.signId,signStatus:num}).then(res=>{
          this.$toast.clear();
                if (res.data.resultCode == 10000) {
          //     this.$router.push({
          //     query:merge(this.$route.query,{signStatus: num})
          // })
          // let query = Object.assign(this.$route.query,{signStatus: num})
          // console.log(query );
          
            this.$router.push({query:Object.assign(this.$route.query,{signStatus: num})})
                // this.$router.push({query: {signStatus: num}})
                this.$dialog
                  .alert({
                    title: res.data.resultDesc
                  })
                  .then(() => {
                    this.$routerto("mysign");
                  });
              }else{
                  this.$dialog
                  .alert({
                    title: res.data.resultDesc
                  })
                  .then(() => {
                  });
              }
       })
// a
    },
    routerto(){
      // this.$store.commit('contract_sign', {
      //   owner: '',
      //   article: '',
      //   agent: '',
      //   owner_signdate: null,
      //   agent_signdate: null,
      // })
        // this.$global.get_encapsulation(`${this.$baseurl}/bsl_web/projectSign/interested`,this.$route.query.projectId).then(res=>{
        // console.log(res);
        
      // })
      this.$routerto('owner_set_contract',this.$route.query)
    },
    agreement(num) {
      this.$dialog
        .confirm({
          title: ""
        })
        .then(() => {
          this.$global.post_encapsulation(`${this.$baseurl}/bsl_web/projectSign/reviewInterestedRequest`,{
                projectId: this.projectId,
                signId: this.signId,
                signStatus: 3
              })
            .then(res => {
              if (res.data.resultCode == 10000) {
                this.$route.query.signStatus=3;
                this.$dialog
                  .alert({
                    title: ""
                  })
                  .then(() => {
                    this.$routerto("mysign");
                  });
              } else if (res.data.resultCode == 10012) {
                this.$dialog
                  .alert({
                    title: ""
                  })
                  .then(() => {});
              }
            });
        })
        .catch(() => {
          // on cancel
        });
    }
  }
};
</script>
<style lang="scss">
#p_sign_request {
  nav {
    // position: relative;
    .van-icon-arrow-left {
      position: absolute;
      left: 0.6rem;
      top: 50%;
      transform: (translate(0, -50%));
    }
  }
}

</style>
<style lang="scss" scoped>
#p_sign_request {
  width: 100%;
  main {
    margin-top: 1.6rem;
    background: #ffffff;
    aside {
      display: flex;
      width: 100%;
      height: 3rem;
      justify-content: center;
    }
    div.investors_infor {
      h2 {
       min-height: 0.68rem;
        font-size: 0.48rem;
        padding: 0.4rem;
        box-sizing: border-box;
        word-break: break-all;
        display: -webkit-flex;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-wrap:  wrap;
        color: #0f6ebe;
        font-weight: 600;
        line-height: 0.68rem;
      }

      // header {
      //   height: 0.8rem;
      //   font-size: 0.42rem;
      //   text-align: center;
      //   background: #f2f2f2;
      //   line-height: 0.8rem;
      //   color: #868686;
      //   // border-bottom: 0.01rem dashed #b5b5b5;
      // }

      ul {
        padding: 0.1rem 0.5rem;
        display: flex;
        flex-direction: column;
        li {
          >div{
          display: flex;
          align-items: baseline;
          font-size: 0.38rem;
          line-height: 0.56rem;
          }
          .row1 {
            color: #4c4c4c;
            font-weight: 600;
            width: 3rem;
          }
          .draft {
            margin-bottom: 0.25rem;
          }
          .row2 {
            width: 7rem;
            word-break: break-all;
            line-height: 0.48rem;
            color: #787878;
          }
          .draft1 {
            padding: 0.2rem 0.4rem;
            box-sizing: border-box;
          }
        }
      }
    }
    article {
      margin: 0 0 1.3rem 0;
      // header {
      //   height: 0.8rem;
      //    font-size: 0.42rem;
      //   text-align: center;
      //   // font-weight: 600;
      //   background: #f2f2f2;
      //   line-height: 0.8rem;
      //   color: #868686;
      //   // border-bottom: 0.01rem dashed #b5b5b5;
      // }
      .nav_lists {
        border-top: 0;
      }
      div.nav_lists {
        display: flex;
        // border-top: 0.2rem solid #f2f2f2;
        border-bottom: 0.2rem solid #f2f2f2;
        > p {
          flex: 1;
          height: 2.5rem;
          font-size: 0.3rem;
          display: flex;
          align-items: center;

          section.box {
            box-sizing: border-box;
            width: 100%;
            display: flex;
            text-align: center;
            height: 2rem;
            //  padding: 0.1rem;
            border-right: 0.08rem solid #f2f2f2;
            flex-direction: column;
            justify-content: space-between;
            span.rowb {
              font-size: 0.6rem;
              color: #0f6ebe;
            }
          }
        }
        p:nth-last-child(1) {
          section.box {
            border-right: 0;
          }
        }
      }
      ul {
        padding: 0.5rem;
        li {
          margin-bottom: 0.1rem;
          display: flex;
          align-items: baseline;
          font-size: 0.38rem;
          .row1 {
            color: #4c4c4c;
            font-weight: 600;
            width: 4rem;
          }
          .draft {
            margin-bottom: 0.25rem;
          }
          .row2 {
            width: 7rem;
            word-break: break-all;
            line-height: 0.48rem;
            color: #787878;
          }
          .draft1 {
            padding: 0.2rem 0.4rem;
            box-sizing: border-box;
          }
        }
        .contract {
          display: block;
          .row2 {
            width: 8rem;
            height: 6rem;
            border: 0.01rem solid #b3b3b3;
            // box-sizing: border-box;
            padding: 0;
            background: #f2f2f2;
            .draft1_middle {
              padding: 0.3rem;
              box-sizing: border-box;
              width: 100%;
              height: 100%;
              overflow-y: auto;
            }
          }
        }
      }
      footer {
        padding: 0 0.5rem 0.5rem 0.5rem;
           font-size: 0.42rem;
        aside {
          height: 2.4rem;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          button {
            height: 1rem;
            color: #ffffff;
          }
          button:nth-of-type(1) {
            background: #00adef;
          }
          button:nth-of-type(2) {
            background: #ff7c2c;
          }
        }
      }
    }
  }
}
</style>



